import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					ipage:0,
					text: '积分收入',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					ipage:0,
					text: '积分支出',
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},
	
	onLoad(options){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		console.log(options)
		if(options.state){				
		this.tabCurrentIndex = +options.state;
		}




		if(options.state == 0){
			this.loadData()
		}

		
	},
	 
	methods: {
		//获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			let ipage = navItem.ipage;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			if(state==0){
				this.$http.post('/cms/member/order/list', {maxPoints:0.01,ipage:ipage}).then(res => {
					navItem.loadingType = 'noMore';
					if(res.data.items&&res.data.items){
						navItem.orderList.push(
							...res.data.items
						);
						navItem.loadingType = 'more';
						this.$set(navItem, 'ipage', parseInt(res.data.pager.ipage)+1);
					}
					this.$set(navItem, 'loaded', true);
					
				}).catch(err => {});
			}else{
				this.$http.post('/cms/member/point/list', {maxPoints:0.01,ipage:ipage}).then(res => {
					navItem.loadingType = 'noMore';
					if(res.data.items&&res.data.items){
						navItem.orderList.push(
							...res.data.items
						);
						navItem.loadingType = 'more';
						this.$set(navItem, 'ipage', parseInt(res.data.pager.ipage)+1);
					}
					this.$set(navItem, 'loaded', true);
					
				}).catch(err => {});
			}
		}, 
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				let {stateTip, stateTipColor} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip, 
					stateTipColor
				})
				
				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val=>val.id === item.id);
				index !== -1 && list.splice(index, 1);
				
				uni.hideLoading();
			}, 600)
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
